export default class GoogleMap extends React.Component {
  constructor(props) {
    super(props);
    this.googleMapRef = React.createRef();
    this.state = {...props.formData};
  }
  
  componentDidMount() {
    const googleMapScript = document.createElement('script')
    googleMapScript.src = "https://maps.googleapis.com/maps/api/js?key=AIzaSyBiNs22RZuqpDA043qxf7LBYOaz6nPu4nk&libraries=places"
    window.document.body.appendChild(googleMapScript)
    
    googleMapScript.addEventListener('load', () => {
      this.map = this.createGoogleMap()
      this.marker = this.createMarker()
    })
  }
  
  clickMarker = () =>  {
    this.doChange("lat",this.marker.getPosition().lat());
    this.doChange("lng",this.marker.getPosition().lng());
  }
  dragendMarker = () =>  {
    this.doChange("lat",this.marker.getPosition().lat());
    this.doChange("lng",this.marker.getPosition().lng());
  }
  
  createGoogleMap = () => {
    return new window.google.maps.Map(this.googleMapRef.current, {
      zoom: 16,
      center: {
        lat: this.props.formData.lat,
        lng: this.props.formData.lng,
      },
      disableDefaultUI: false,
    })
  }

  createMarker = () => {
    var marker = new window.google.maps.Marker({
      position: { 
        lat: this.props.formData.lat,
        lng: this.props.formData.lng,
      },
      draggable: true,
      map: this.map,
    })
    marker.addListener('click', this.clickMarker);
    marker.addListener('dragend', this.dragendMarker);
    return marker;
  }
  
  onChange(name) {
    return (event) => {
      var LatLng = {
        lat : this.marker.getPosition().lat(),
        lng : this.marker.getPosition().lng(),
      }
      LatLng[name] = parseFloat(event.target.value);
      if(parseFloat(event.target.value)) {
        this.map.setCenter(LatLng)
        this.marker.setPosition(LatLng);
      }
      this.setState({
        [name]: parseFloat(event.target.value)
      }, () => this.props.onChange(this.state));
    };
  }
  
  doChange(name,value) {
    this.setState({
      [name]: value
    }, () => this.props.onChange(this.state));
  }  

  render() {
    const {lat, lng} = this.state;
    const {props} = this;
    const mapStyles = {
      width: '100%', 
      height: '150px'  
    };
    return (
      <fieldset>
        <legend>{props.schema.title}</legend>
        <div className={props.uiSchema.classNames}>
          <div
            id="google-map"
            ref={this.googleMapRef}
            style={mapStyles}
          >
          </div>
          <div className={props.uiSchema.classNames}>
            <label>{props.schema.properties.lat.title}{props.schema.required[0] ? "*" : null}</label>
            <input type="number" value={lat} onChange={this.onChange("lat")} required /><br/>
            <label>{props.schema.properties.lng.title}{props.schema.required[1] ? "*" : null}</label>
            <input type="number" value={lng} onChange={this.onChange("lng")} required />
          </div>
        </div>
      </fieldset>    
    )
  }
}

