class ZfileComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {...props.formData};
  }
  
  uploadFile(file) {
    const { name, size, type } = file;
    return new Promise((resolve, reject) => {
      const formData = new FormData();
      formData.append('file', file);
      fetch('http://103.80.51.21:5566/repos/upload', {
        method: 'POST',
        mode: 'cors',
        cache: 'no-cache',
        body: formData
      })
      .then((response) => response.json())
      .then((result) => {
        resolve(result);
      })
      .catch((error) => {
        reject(error);
      });
    });
  }
  
  onFileChange(name) {
    return (event) => {
      const me = this;
      this.uploadFile(event.target.files[0]).then((uF)=>{
        const data = uF.url;
        me.setState({
          [name]: data
        }, () => me.props.onChange(me.state));        
      })
    };
  }

  onDetailChange(name) {
    return (event) => {
      this.setState({
        [name]: event.target.value
      }, () => this.props.onChange(this.state));
    };
  }  

  render() {
    const { data, detail } = this.state;
    const { props } = this;
    const { multiple, id, readonly, disabled, autofocus, options } = this.props;
    const xShow = {display: 'block'};
    const xHide = {display: 'none'};
    var dataClass;
    if(typeof data == 'undefined') {
      dataClass = xHide;
    }else{
      dataClass = xShow
    }
    return (
      <fieldset>
        <legend>{props.schema.title}</legend>
        <div className={props.uiSchema.classNames}>
          <label className="control-label">{props.schema.properties.data.title}{props.required ? "*" : null}</label>
          <input
            id={id}
            type="file"
            disabled={readonly || disabled}
            onChange={this.onFileChange("data")}
            autoFocus={autofocus}
            accept="image/*"
            //accept={options.accept}
          /><br/>
          <label className="control-label">{props.schema.properties.detail.title}{props.required ? "*" : null}</label>
          <input
            id={id}
            className="form-control"
            type="string"
            disabled={readonly || disabled}
            onChange={this.onDetailChange("detail")}
            autoFocus={autofocus}
            value={detail}
            //placeholder="คำอธิบายรูป"
            //accept="image/*"
            //accept={options.accept}
          /><br/>
          <img style={dataClass} src={data} height="150" alt="Image preview..."/>
        </div>
      </fieldset>    
    );
  }
}

