function ObjectFieldTemplate(props) {
  return (
    <fieldset>
    <legend>{props.schema.title}</legend>
    <div>
      {props.description}
      {props.properties.map(element => <div className="property-wrapper">{element.content}</div>)}
    </div>
    </fieldset>  
  );
}

